<template>
  <div class='selectDoctor-box'>
    <van-dropdown-menu class="mb-10">
      <van-dropdown-item v-model="departmentVal" :options="departmentList" />
      <van-dropdown-item v-model="jobtitleVal" :options="jobtitleList" />
      <van-dropdown-item v-model="dateVal" :options="dateList" />
    </van-dropdown-menu>
    <div class="doctor-list-box">
      <div v-for="(item,index) in 10" class="mb-5 ml-5 mr-5" :key="index">
          <van-card
            desc="职位描述"  
            title="医生名字"
            thumb="https://img.yzcdn.cn/vant/t-thirt.jpg"
          >
            <div slot="footer">
              <van-button  @click="goDoctorspaceFn">预约</van-button>
            </div>
          </van-card>
        </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: '',
    data(){
      return{
        departmentList:[{text:'口腔科',value:0},{text:'烧伤整形科',value:1},{text:'儿科',value:2}],
        jobtitleList:[{text:'全部',value:0},{text:'主任',value:1},{text:'副主任',value:2}],
        dateList:[{text:'今天',value:0},{text:'明天',value:1}],
        departmentVal:0,
        jobtitleVal:0,
        dateVal:0
      }
    },
    methods:{
      goDoctorspaceFn(){
        this.$router.push({path:'/doctorSpace',query:{setid:123456}})

      }
    }
  };
</script>
<style lang='scss'>
.selectDoctor-box{
  .van-card{
    position: relative;
    background: #fff;
    .van-card__title{
      font-size: 16px;
      margin-bottom: 5px;
    }
    .van-card__footer{
      position: absolute;
      top: 10px;
      right: 10px;  
    }
  }
}
</style>


